<template>
  <div :class="classes" :style="{flexBasis:this.size,order:this.order}">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: ["align", "size", "shrink", "grow", "order"],
  data() {
    return {
      classes: {
        flexitem: true,
        "align-start": this.align == "start",
        "align-end": this.align == "end",
        "align-center": this.align == "center",
        "align-baseline": this.align == "baseline",
        "align-stretch": this.align == "stretch",
        shrink: this.shrink,
        grow: this.grow
      }
    };
  }
};
</script>

<style>
.flexitem {
  align-self: auto;
}
.flexitem.align-start {
  align-self: flex-start;
}
.flexitem.align-end {
  align-self: flex-end;
}
.flexitem.align-center {
  align-self: center;
}
.flexitem.align-baseline {
  align-self: baseline;
}
.flexitem.align-stretch {
  align-self: stretch;
}
.flexitem.grow {
  flex-grow: 1;
}
.flexitem.shrink {
  flex-shrink: 1;
}
</style>
