<template>
  <div :class="classes">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: ["direction", "wrap", "wrap_reverse", "justify", "align"],
  data() {
    return {
      classes: {
        flexgrid: true,
        column: this.direction === "column",
        column_reverse: this.direction === "column-reverse",
        row_reverse: this.direction === "row-reverse",
        wrap_reverse: this.wrap_reverse === "",
        wrap: this.wrap === "",
        justify_start: this.justify === "start",
        justify_end: this.justify === "end",
        justify_center: this.justify === "center",
        justify_space_between: this.justify === "space-between",
        justify_space_around: this.justify === "space-around",
        justify_space_evenly: this.justify === "space-evenly",
        align_start: this.align === "start",
        align_end: this.align === "end",
        align_center: this.align === "center",
        align_stretch: this.align === "stretch",
        align_baseline: this.align === "baseline",
        align_middle: this.align === "middle"
      }
    };
  }
};
</script>

<style>
.flexgrid {
  display: flex;
}
.flexgrid.column {
  flex-direction: column;
}
.flexgrid.column_reverse {
  flex-direction: column-reverse;
}
.flexgrid.row_reverse {
  flex-direction: row-reverse;
}
.flexgrid.wrap {
  flex-wrap: wrap;
}
.flexgrid.wrap_reverse {
  flex-wrap: wrap-reverse;
}
.flexgrid.justify_start {
  justify-content: flex-start;
}
.flexgrid.justify_end {
  justify-content: flex-end;
}
.flexgrid.justify_center,.flexgrid.align_middle {
  justify-content: center;
}
.flexgrid.justify_space_between {
  justify-content: space-between;
}
.flexgrid.justify_space_around {
  justify-content: space-between;
  justify-content: space-around;
}
.flexgrid.justify_space_evenly {
  justify-content: space-between;
  justify-content: space-around;
  justify-content: space-evenly;
}
.flexgrid.align_start {
  align-items: flex-start;
}
.flexgrid.align_end {
  align-items: flex-end;
}
.flexgrid.align_center,.flexgrid.align_middle {
  align-items: center;
}
.flexgrid.align_baseline {
  align-items: baseline;
}
.flexgrid.align_stretch {
  align-items: stretch;
}
</style>
